<template>
  <div class="section1">
    <div class="container">
      <div class="row pt-4 pb-5">
        <!-- Review -->
        <div class="col-12 col-md-8 p-2" id="review">
          <div class="box flex justify-center align-center border-2">
            <swiper :options="swiperOption">
              <swiper-slide v-for="(slide, index) in swiperSlides" :key="index" style="display: block">
                <a href="#">
                  <img v-lazy="'/static/images/preview/preview_0' + slide + '.jpg'">
                </a>
              </swiper-slide>
              <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
          </div>
        </div>
        <!-- Promotion -->
        <div class="col-12 col-md p-2" id="promotion">
          <div class="box">
            <div class="row m-0 h-100">
              <!-- Promotion Box 1 -->
              <div class="col-6 p-0 col-md-12 mb-2 " id="promotion-1">
                <div class="child-box flex justify-center align-center border-2">
                  <img v-lazy="'/static/images/promotion/promotion_01.jpg'" class="border-2">
                </div>
              </div>
              <!-- Promotion Box 2 -->
              <div class="col-6 p-0 col-md-12 mt-2" id="promotion-2">
                <div class="child-box flex justify-center align-center border-2">
                  <img v-lazy="'/static/images/promotion/promotion_02.jpg'" class="border-2">
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
export default {
  name: 'Section1',
  components: {
    swiper,
    swiperSlide
  },
  data () {
    return {
      swiperOption: {
        dragable: false,
        autoplay: {
          delay: 5000
        },
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
      },
      swiperSlides: [1, 2, 3, 4, 5],
    }
  }
}
</script>

<style>
</style>



